@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
html{min-height:100%;box-sizing:border-box}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Montserrat', sans-serif}

.App{display:grid;min-height:100vh;grid-template-rows:1fr 1.5rem;background-color:#fafafa}.app-content{padding:1em}

.nav{display:-webkit-flex;display:flex;border:solid 1px black;margin:0;padding:0;background-color:#0d61f2;color:white;list-style:none}.nav:first-child{margin-left:auto}


.react-tabs{-webkit-tap-highlight-color:transparent}.react-tabs__tab-list{border-bottom:1px solid #aaa;margin:0 0 10px;padding:0}.react-tabs__tab{display:inline-block;border:1px solid transparent;border-bottom:none;bottom:-1px;position:relative;list-style:none;padding:6px 12px;cursor:pointer}.react-tabs__tab--selected{background:#fff;border-color:#aaa;color:black;border-radius:5px 5px 0 0}.react-tabs__tab--disabled{color:GrayText;cursor:default}.react-tabs__tab:focus{box-shadow:0 0 5px #0188fe;border-color:#0188fe;outline:none}.react-tabs__tab:focus:after{content:"";position:absolute;height:5px;left:-4px;right:-4px;bottom:-5px;background:#fff}.react-tabs__tab-panel{display:none}.react-tabs__tab-panel--selected{display:block;overflow-y:scroll;height:600px}

.question-title-area{text-align:center;border:solid 1px black;margin-bottom:1rem;border-radius:1rem;background-color:#f0f4f9;box-shadow:#b8b9be 3px 3px 6px,#fff -3px -3px 6px;border-color:#d1d9e6}.question-area{display:-webkit-flex;display:flex;height:100%;width:100%;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;margin-bottom:1em;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;grid-row-gap:1rem;row-gap:1rem}@media screen and (max-width: 600px){.question-area{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}}.question-area-video{-webkit-flex-basis:50%;flex-basis:50%;width:50%;padding:1rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;border:solid 1px black;border-radius:1rem;background-color:#f0f4f9;box-shadow:#b8b9be 3px 3px 6px,#fff -3px -3px 6px;border-color:#d1d9e6}@media screen and (max-width: 900px){.question-area-video{-webkit-flex-basis:90%;flex-basis:90%;width:90%}}.question-area-selection{-webkit-flex-basis:40%;flex-basis:40%;border:solid 1px black;margin-bottom:1rem;border-radius:1rem;background-color:#f0f4f9;box-shadow:#b8b9be 3px 3px 6px,#fff -3px -3px 6px;border-color:#d1d9e6;padding:1rem}@media screen and (max-width: 900px){.question-area-selection{-webkit-flex-basis:90%;flex-basis:90%;width:90%}}.recording-option{padding:0.5em;width:50%;text-overflow:wrap;font-weight:500;font-size:1em}.recording-option:hover{cursor:pointer}.question-selection-button{width:100%;padding:1em}.question-selection-button:hover{cursor:pointer;background-color:#a1ffa1}

.footer{background-color:#0d61f2;text-align:center;margin:0}.footer a{text-decoration:none;color:white}

